import "../utils/index"
const components = [];
// ÂÆö‰πâinstallÊñπÊ≥ï
const install = function (Vue) {
  if (install.installed) return;
  // Ê≥®ÂÜåÊâÄÊúâÁöÑÁªÑ‰ª∂
  components.forEach((component) => {
    Vue.component(component.name, component);
  });
};
// Âà§Êñ≠ÊòØÂê¶Áõ¥Êé•ÂºïÂÖ•Êñá‰ª∂ÔºåÂ¶ÇÊûúÊòØÔºåÂ∞±‰∏çÁî®Ë∞ÉÁî®Vue.use()
if (typeof window !== "undefined" && window.Vue) {
  install(window.Vue);
}
// ÂØºÂá∫installÊñπÊ≥ï
export default {
  install,
  ...components,
};
