// ÂºïÂÖ•ÊèêÁ§∫Ê°ÜÈ°µÈù¢ÁªÑ‰ª∂
import Modal from "./updateModal.vue";
import Vue from "vue";

let time = 0; // ËÆ°ÁÆóËΩÆËØ¢Ê¨°Êï∞
let version = ""; // ÁºìÂ≠òÁöÑÁâàÊú¨Âè∑
let prodFlag = process.env.NODE_ENV === "production"; // ÊòØÂê¶ÊòØÁîü‰∫ßÁéØÂ¢É
console.log(process.env.NODE_ENV, prodFlag, "prodFlag")
let timer = null; // ËΩÆËØ¢ÂÆöÊó∂Âô®
// ËΩÆËØ¢Áî®Ê£ÄÊµãÊñπÊ≥ï
let timerFuncion = async () => {
    // Ê¨°Êï∞Ë∂ÖËøáÁöÑÊó∂ÂÄô ÂÅúÊ≠¢ËΩÆËØ¢ Èò≤Ê≠¢Áî®Êà∑ÊåÇÁùÄÁΩëÈ°µ‰∏ÄÁõ¥ËΩÆËØ¢
    if (time >= 5) {
        // ‰ªÖÊ∏ÖÈô§ËÆ°Êó∂Âô®
        clearInterval(timer);
        return (timer = null);
    }
    // fetch ÈÉ®ÁΩ≤ÂêéÂêåÂ±ÇÁ∫ßÁöÑversionÊñá‰ª∂ Âπ∂‰∏îÂä†‰∏äÊó∂Èó¥Êà≥ÂèÇÊï∞ Èò≤Ê≠¢ÂéªËÆøÈóÆÊú¨Âú∞Á°¨ÁõòÁöÑÁºìÂ≠ò
    let res = await fetch(`/version.txt?v=${new Date().getTime().toString()}`)
        .then((res) => {
            return res.json();
        })
        .catch((err) => {
            console.log(err);
            return clearTimer(); // ËÆøÈóÆÂ§±Ë¥•Â∞±ÂÆåÂÖ®ÂÖ≥Èó≠ËΩÆËØ¢
        });

    // È¶ñÊ¨°Âä†ËΩΩÁΩëÈ°µÁöÑÊó∂ÂÄô Â≠òÂÇ®Á¨¨‰∏Ä‰ªΩversion
    if (!version) {
        version = res;
        console.log("È¶ñÊ¨°Âä†ËΩΩÁâàÊú¨", version);
    } else if (version != res) {
        console.log("ÂèëÁé∞ÁâàÊú¨Êõ¥Êñ∞", version);
        // ÂºπÂá∫Êõ¥Êñ∞ÊèêÁ§∫ ÂèëÁé∞verisonÊñá‰ª∂Êõ¥Êñ∞‰∫Ü Â∞±‰ª£Ë°®Êñ∞ÈÉ®ÁΩ≤‰∫Ü
        // ÂÄüÈâ¥ElementÁöÑMessageÂÆûÁé∞ÊåÇËΩΩvueÁªÑ‰ª∂Âà∞È°µÈù¢‰∏ä
        let MessageConstructor = Vue.extend(Modal);
        let instance = new MessageConstructor({
            data: {},
        });
        instance.id = new Date().getTime().toString();
        instance.$mount();
        document.body.appendChild(instance.$el);
        return clearTimer();
    }

    time++;
};
// Ê£ÄÊµãÈº†Ê†áÊòØÂê¶ÁßªÂä® ÁßªÂä®‰ª£Ë°®Áî®Êà∑Ê¥ªË∑É‰∏≠ ÊääËΩÆËØ¢ÊØîËæÉÁî®ÁöÑÊ¨°Êï∞‰∏ÄÁõ¥Ê∏Ö0
let moveFunction = () => {
    // Âà§Êñ≠Âú®Áîü‰∫ßÁéØÂ¢É‰∏ãÊâçÊâßË°å
    if (prodFlag) {
        time = 0;
        // ÈïøÊó∂Èó¥ÊåÇÊú∫Âêé ‰∏çÂú®ËΩÆËØ¢ÁöÑÁΩëÈ°µ Âú®Èº†Ê†áÊ¥ªË∑É‰∫éÁ™óÂè£ÁöÑÊó∂ÂÄôÈáçÊñ∞Ê£ÄÊµã
        if (!timer) {
            timer = setInterval(timerFuncion, 1000);
        }
    }
};
// ÂΩìË¢´main.js ÂºïÁî®ÁöÑÊó∂ÂÄô ÂºÄÂßãËΩÆËØ¢‰∫éÁõëÂê¨Èº†Ê†áÁßªÂä®‰∫ã‰ª∂
if (prodFlag) timer = setInterval(timerFuncion, 5000);
window.addEventListener("mousemove", moveFunction);
// ÂÆåÂÖ®Ê∏ÖÈô§ËΩÆËØ¢ ‰∏çËΩÆËØ¢ ‰∏çÁõëÂê¨Èº†Ê†á‰∫ã‰ª∂
let clearTimer = () => {
    clearInterval(timer);
    window.removeEventListener("mousemove", moveFunction);
    timer = null;
};

